  html{
    font-size: 62.5%;/* 16px x 62.5 = 10px = 1rem*/
    box-sizing: border-box;
  }
  body{
    margin: 0;
    height: 100vh;
    font-size: 1.6rem;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  #root{
    height:100%;
  }
  /* Layout*/
  .grid-container{
    display: grid;
    grid-template-areas: 
    'header' 
    'main' 
    'footer';
    grid-template-columns: 1fr;
    grid-template-rows: 5rem 1fr 5rem;
    height: 100%;
  }
  header{
    grid-area: header;
    background-color: #203040;
  }
  main{
    grid-area: main;
    padding: 1rem;
  }
  footer{
    grid-area: footer;
    background-color: #203040;
    color: #ffffff;
   
  }
  .row{
    display:flex;
    flex-wrap: wrap;
    justify-content: right;
    align-items: center;
  }
  .row.center{
    justify-content: center;
  }
  header a {
    color: #ffffff;
    padding: 1rem;
  }
  a.brand{
    color: #ffffff;
    font-size: 3rem;
    font-weight: bold;
  }
  a {
    text-decoration: none;
  }
  a:hover{
    color:#ff8000
  }
  a.brand{
    color: #ffffff;
    font-size: 3rem;
    font-weight: bold;
  }
